div {
	align-content: center;
	font-family: 'Nunito', Arial, Roboto;
	margin-bottom: 50px;
}

.title {
	margin-top: 50px;
	font-family: Nunito;
	text-align: center;
	text-transform: uppercase;
}

.title-small {
	font-size: 1em;
}

.title-big {
	font-size: 1.7em;
}

.intro {
	width: 300px;
	text-align: center;
	margin: auto;
	margin-top: 30px;
}

.logo {
	width: 200px;
	height: 200px;
	border-style: solid;
	margin: auto;
}

.text-box {
	width: 1300px;
	height: 290px;
	display: block;
	margin: auto;
}

.left,
.right {
	width: 440px;
	height: 250px;
	border-width: 1px;
	border-style: solid;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 30px;
	padding: 20px;
}

.left {
	float: left;
}

.right {
	float: right;
}

.title {
	text-transform: uppercase;
	font-size: larger;
}

.animation-box {
	width: 1300px;
	height: 290px;
	display: block;
	margin: auto;
}

.left,
.right {
	width: 440px;
	height: 250px;
	border-width: 1px;
	border-style: solid;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 30px;
	padding: 20px;
}

.left {
	float: left;
}

.right {
	float: right;
}


#css {
	fill: #7000ff;
}
#js {
	fill: #00a3ff;
}
#web-gl {
	fill: #ff00b8;
}
#flash {
	fill: #bd00ff;
}
#canvas {
	fill: #0057ff;
}

circle {
	fill: white;
	stroke: #000;
	stroke-miterlimit: 10;
	stroke-width: 0.5202428102493286px;
}
line {
	fill: none;
	stroke: #000;
	stroke-miterlimit: 10;
	stroke-width: 0.5202428102493286px;
}

.background {
	position: flex;
}

#tooltip {
	position: absolute;
	background: violet;
	border: solid black 1px;
	border-radius: 15%;
	padding: 5px;
	z-index: 100;
}

